<script>
// UI
import Select from '@/components/ui/Select'
import Calendar from '@/components/ui/Calendar'

export default {
	name: 'TargetReport',

	components: {
		'ui-select': Select,
		'ui-calendar':Calendar
	},
	beforeMount() {
		this.setDatas()
	},
	mounted() {
		this.sortTableAutoWidth()
	},
	data () {
		return {
			categorySelectData: {
				emptyText: '',
				textList: [
					'',
					'',
					'',
					'',
					'',
					'NGO',
					'',
					'',
					''
				]
			},
			accountSelectData: {
				emptyText: '',
				textList: [
					'',
					'1',
					'2',
					'3'
				]
			},
			sortSelectData: {
				emptyText: '  ',
				onShow:false,
				listData: [],
			},

			listData:[
				{
					number:1,
					name:''
				},
				{
					number:2,
					name:''
				},
				{
					number:3,
					name:''
				}
			],

			tablesAutoWidth:4880,



			show: false,
			time: new Date(),
			range: [new Date(),new Date()],
			emptyTime: '',
			emptyRange: [],
			local: {
				type: Object,
				default () {
					return {
						dow: 0, // Sunday is the first day of the week
						hourTip: 'Select Hour', // tip of select hour
						minuteTip: 'Select Minute', // tip of select minute
						secondTip: 'Select Second', // tip of select second
						yearSuffix: '', // suffix of head year
						yearSuffix: '', // format of head
						monthsHead: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'), // months of head
						months: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'), // months of panel
						weeks: '______'.split('_') // weeks
					}
				}
			}
		}
	},

	methods: {
		setDatas() {

			const label = ['','','','','','',' ',' ','','','','','  ','   ',' ',' ','  ']
			const sortData = []

			for(let i = 0; i < label.length; i++) {
				let setting = {
					setting:{
						name:label[i],
						checkId:'sort' + i,
						show:true,
						checked:true
					}
				}
				this.sortSelectData.listData.push(setting)
			}
		},
		selectCategory(item) {
			this.categorySelectData.emptyText = item
		},
		selectAccount(item) {
			this.accountSelectData.emptyText = item
		},
		sortSelectOnOff() {
			this.sortSelectData.onShow = !this.sortSelectData.onShow
		},
		sortSelectFilter(item) {
			const me = this
			item.setting.checked = !item.setting.checked
			item.setting.show = !item.setting.show
			setTimeout(function() {
				me.sortTableAutoWidth()
			}, 1)
		},
		sortTableAutoWidth(){
			const listEl = document.getElementById('report-list')
			const ulEl = document.getElementById('report-list-head')
			const liEl = ulEl.getElementsByClassName('report-line')
			let listWidth = 0

			for(let i = 0; i < liEl.length; i++) {
				listWidth += liEl[i].offsetWidth
			}

			this.tablesAutoWidth = listWidth
			listEl.style.width = this.tablesAutoWidth + 'px'
		},
		disabledDate (time) {
			return time < this.min || time > this.max
		},
		dc (e) {
			this.show = this.$el.contains(e.target) && !this.disabled
		}
	}
}
</script>

